import echarts from 'echarts'

export const chartUtils = {
  initColumn: (id, legend, title, color, data) => {
    const myChart = echarts.init(document.getElementById(id));
    const option = {
      title: {
        subtext: title
      },
      color: color,
      grid: {
        left: '1%',
        right: '2%',
        containLabel: true
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        y:'bottom',
        data: legend,
        itemHeight:10,
        itemWidth:12,
        textStyle: {
          fontSize: 10
        }
      },
      calculable: true,
      xAxis: [
        {
          type: 'category',
          data: [''],
        }
      ],
      yAxis: [
        {
          type: 'value'
        }
      ],
      series: data
    };
    myChart.setOption(option);
  },
  initPie:(id,legend,title,color,data)=>{
    const myChart = echarts.init(document.getElementById(id));
    const option = {
      color: color,
      title: {
        subtext: title,
        textStyle: {
          fontSize: 12,
        }
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)'
      },
      legend: {
        // orient: 'vertical',
        y:'bottom',
        data: legend,
        itemHeight:10,
        itemWidth:12,
        textStyle: {
          fontSize: 10
        }
      },
      series: [
        {
          name: title,
          type: 'pie',
          radius: ['50%', '70%'],
          center: ['50%','50%'],  //第一个百分比是左右移动，50%居中，越小越靠左，反之亦然；第二个百分比是上下移动，50%居中，越小越靠上反之亦然。
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data:data
        }
      ]
    };
    myChart.setOption(option);

  },

  initPieKpi:(id,legend,title,color,data)=>{
    const myChart = echarts.init(document.getElementById(id));
    const option = {
      color: color,
      title: {
        subtext: title,
        textStyle: {
          fontSize: 12,
        }
      },
      tooltip: {
        trigger: 'item',
        formatter: '{a} <br/>{b}: {c} ({d}%)'
      },
      legend: {
        // orient: 'vertical',
        y:'bottom',
        data: legend,
        itemHeight:10,
        itemWidth:12,
        textStyle: {
          fontSize: 10
        }
      },
      series: [
        {
          name: title,
          type: 'pie',
          radius: ['50%', '70%'],
          center: ['50%','50%'],  //第一个百分比是左右移动，50%居中，越小越靠左，反之亦然；第二个百分比是上下移动，50%居中，越小越靠上反之亦然。
          avoidLabelOverlap: false,
          label: {
            normal: {
              show: false,
              position: 'center'
            },
            emphasis: {
              show: true,
              textStyle: {
                fontWeight: 'bold'
              }
            }
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data:data
        }
      ]
    };
    myChart.setOption(option);

  },

  initLine: (id) => {
    const myChart = echarts.init(document.getElementById(id));
    const option = {
      title: {
        subtext: '折线图'
      },
      color: '#92D050',
      grid: {
        left: '1%',
        right: '2%',
        bottom: '10',
        containLabel: true
      },
      tooltip: {
        trigger: 'axis'
      },
      legend: {
        show: false
      },
      calculable: true,
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: ['12:00:00', '12:03:00', '12:06:00', '12:09:00', '12:12:00', '12:15:00']
      },
      yAxis: [
        {
          type: 'value',
          splitNumber: 6
        }
      ],
      series: {
        data: [-120,-20,-120,-20,0,50],
        type: 'line',
        smooth: 0.6,
        areaStyle: {}
      }
    };
    myChart.setOption(option);
  }

}




